import * as React from "react"
import { Link } from "gatsby"

import multigo from "../images/multigo.jpg"

class WebProjects extends React.Component {
  constructor() {
    super()
    this.state ={ portfolioData: [
      {
        title: "MultiGo Landing Page",
        description: "Experiment and Study on HTML5, CSS3, Bootstrap, jQuery",
        image: multigo,
        link: "/multigo-demo",
      },
    ]}
  }

  render() {
    return (
      <div className="mt-12">
        <div className="text-center text-lumerb-dark">
          <h1>Websites and Experiments</h1>
          <p className="text-gray-500">
            It is highly important for me to be updated on the current trends
            and technologies used in web development.
            <br />
            Here are some of my experiments and studies with different
            technologies, libraries, frameworks and more...
          </p>
        </div>

        <div className="grid grid-cols-1 auto-rows-fr p-10 max-w-7xl mx-auto sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-3 gap-5">
          {this.state.portfolioData.map((portfolio, key) => {
            console.log(portfolio.image)
            return (
              <div key={key} className="card-lumerb transition duration-500 ease-in-out rounded overflow-hidden shadow-md hover:bg-gray-50 hover:shadow-sm">
              <Link style={{ textDecoration: "none" }} to={portfolio.link}>
                <div className="flex flex-col h-full">
                  <div className="mb-0">
                    <img
                      className="mb-0 w-full portfolio-image"
                      src={portfolio.image}
                      alt={portfolio.title}
                    />
                  </div>
                  <div className="px-6 py-4 flex-grow">
                    <div className="font-bold text-gray-500 text-xl mb-2">
                      {portfolio.title}
                    </div>
                    <p className="text-gray-400 text-sm">
                      {portfolio.description}
                    </p>
                  </div>
                  <div
                    style={{ marginTop: "auto" }}
                    className="text-xs text-gray-300 px-6 py-4"
                  >
                    View Demo Site
                  </div>
                </div>
              </Link>
            </div>)
          })}
        </div>
      </div>
    )
  }
}

export default WebProjects
